let url = `${process.env['REACT_APP_BASE_URL']}`

let login = JSON.parse(sessionStorage.getItem("loginStore"));

const x = (x)=> {
  if (x.status === 200) return x.json()
  if (x.status === 401) document.getElementById("login_fail").style.display = "block"
  return
}


const aa = function (Payload) {
  return {
    method: 'post',
    body: JSON.stringify(Payload),
    headers: new Headers({
      'content-type': 'application/json',
      'Authorization': 'Bearer ' + login,
    })
  }
}

 

export const ApiAutenticacao = async (history, PayLoadLogin) => await fetch(`${url}/Authenticate`,
  {
    method: 'POST',
    headers: new Headers({
      'content-type': 'application/json'
    }),
    body: PayLoadLogin
  })
  .then(x)
  .then(x => {
    if (x) {
      login = x.user
      sessionStorage.setItem("loginStore", JSON.stringify(login));
      history.push("/");
      return x
    }
  })


export const fullpage_index = (idPagina) => fetch(`${url}/GetPaginas`, {
  method: 'post',
  body: JSON.stringify(idPagina),
  headers: new Headers({
    'content-type': 'application/json',
    'Authorization': 'Bearer ' + login,
  })
})
  .then(x => x.json());


export const GetPontos = (latLong, history) => fetch(`${url}/Ponto/GetPontos`, aa(latLong))
  .then(x => {
    if (x.status === 200) return x.json()
    if (x.status === 401) history.push("/Login")
    return
  })





