import React, { Component } from "react";
import { fullpage_index } from "../../Api";
import MyCanvas from "./components/MyCanvas.js"
import IFrame from "./components/IFrame.js"
import Img from "./components/Img.js"
import "./pagina.css";
import TopButton from "./components/topButton.js"


class Page {
  pagina = null;
  concessionaria_pagina = null;
  sites = null;
  contatos = null;
  obj = null;


  constructor(dados) {
    const { nome_pagina, endereco, info, sites, contatos } = dados.dados;
    this.contatos = contatos;
    this.pagina = { nome_pagina, endereco, info };
    this.concessionaria_pagina = { info };
    const obj = {};
    for (var i = 0; i < sites.length; i++) {
      if (!obj[sites[i].tipo]) obj[sites[i].tipo] = [];
      obj[sites[i].tipo].push(sites[i].link);
    }
    this.obj = obj;
  }

  get_infos() {
    return {
      pagina: this.pagina,
      concessionaria_pagina: this.concessionaria_pagina,
      sites: this.sites,
      contatos: this.contatos,
      obj: this.obj,
    };
  }
}





class App extends Component {
  state = {
    pagina: false,
    concessionaria_pagina: 0,
    contatos: [],
    STREET: [],
    foto: [],
    WIKIMAPIA_SAT: [],
    WIKIMAPIA_FRIO: [],
    pm: [],
    site: [],
    abcr: [],
    concessionaria: [],
    FOTO_MAPA: [],
    WIKIPEDIA: [],
    LEI: [],
  };





  async componentDidMount() {

    document.getElementById("Pagina").style.display = "none";
    document.getElementById("loader").style.display = "block";
    document.body.style.height = 0 + 'px'


    const { id_pagina } = this.props

    const v = await id_pagina

    if (process.env['REACT_APP_BASE_URL4'] === 'development') {
      // console.log(v)
    }

    const dados = await fullpage_index(v);
    if(dados === undefined) {
      return
    }
    const { nome_pagina } = dados.dados


    try {
      if (!nome_pagina) return;
      const page = new Page(dados);
      const { pagina, concessionaria_pagina, contatos, obj } = page.get_infos();
      document.title = `BAHM - ${pagina.nome_pagina}`;
      if (contatos !== undefined) this.setState({ contatos });

      const {
        STREET,
        LEI,
        FOTO_MAPA,
        FOTO,
        WIKIMAPIA_FRIO,
        WIKIMAPIA_SAT,
        WIKIPEDIA,
        PM,
        SITE,
        ABCR,
        CONCESSIONARIA,
      } = obj;

      this.setState({
        pagina,
        concessionaria_pagina,
        STREET: STREET ? STREET : [],
        LEI: LEI ? LEI : [],
        FOTO_MAPA: FOTO_MAPA ? FOTO_MAPA : [],
        WIKIMAPIA_FRIO: WIKIMAPIA_FRIO ? WIKIMAPIA_FRIO : [],
        WIKIMAPIA_SAT: WIKIMAPIA_SAT ? WIKIMAPIA_SAT : [],
        WIKIPEDIA: WIKIPEDIA ? WIKIPEDIA : [],
        concessionaria: CONCESSIONARIA ? CONCESSIONARIA : [],
        pm: PM ? PM : [],
        site: SITE ? SITE : [],
        abcr: ABCR ? ABCR : [],
        foto: FOTO ? FOTO : [],
      });

      document.getElementById("loader").style.display = "none";
      document.body.style.height = '100%'
      document.getElementById("Pagina").style.display = "block";

    } catch (error) {
      alert('Pagina não encontrada')
    }
  }

  render() {
    return (
      <>
        <div id="Pagina" style={{ display: 'block' }}>
          {<button onClick={() => {
            this.setState({
              pagina: false,
              concessionaria_pagina: 0,
              contatos: [],
              STREET: [],
              foto: [],
              WIKIMAPIA_SAT: [],
              WIKIMAPIA_FRIO: [],
              pm: [],
              site: [],
              abcr: [],
              concessionaria: [],
              FOTO_MAPA: [],
              WIKIPEDIA: [],
              LEI: []
            })

            this.props.trocaTela(undefined)

          }} style={{
            position: 'absolute',
            marginTop: 40,
            marginLeft: 100,
            backgroundColor: 'white',
            padding: 18,
            fontWeight: 'bold',
            borderRadius: 10,
          }}>
            <svg style={{
              marginRight: 10
            }} xmlns="http://www.w3.org/2000/svg" width="20" height="20" fill="currentColor" className="bi bi-arrow-left-circle-fill" viewBox="0 0 16 16">
              <path d="M8 0a8 8 0 1 0 0 16A8 8 0 0 0 8 0zm3.5 7.5a.5.5 0 0 1 0 1H5.707l2.147 2.146a.5.5 0 0 1-.708.708l-3-3a.5.5 0 0 1 0-.708l3-3a.5.5 0 1 1 .708.708L5.707 7.5H11.5z" />
            </svg>
            VOLTAR</button>}
          {<div id="principal">

            {this.state.pagina && <MyCanvas dados={this.state} />}
            {[
              this.state.STREET,
              this.state.foto,
              this.state.WIKIMAPIA_SAT,
              this.state.WIKIMAPIA_FRIO,
              this.state.pm,
              this.state.site,
              this.state.abcr,
              this.state.concessionaria,
              this.state.FOTO_MAPA,
              this.state.LEI,
              this.state.WIKIPEDIA,
            ].map((data, index) => {

              return data.map((data) => {
                if (index === 1 && data.indexOf('http') === -1)
                  return (
                    <div>

                      <br />
                      <br />
                      <br />
                      <Img key={data} dados={`${process.env['REACT_APP_BASE_URL2']}${data}`} />
                    </div>
                  );



                if (index === 8 && data.indexOf('http') === -1)
                  return (
                    <div>
                      <br />
                      <br />
                      <br />

                      <Img key={data} dados={`${process.env['REACT_APP_BASE_URL2']}${data}`} />
                    </div>
                  );


                if (index === 8)
                  return (
                    <div>
                      <br />
                      <br />
                      <br />

                      <Img key={data} dados={data} />
                    </div>
                  );




                return (
                  <div>
                    <br />
                    <br />
                    <br />
                    <IFrame key={data} dados={data} />
                  </div>
                );
              });
            })}

          </div>
          }
          <TopButton/>
        </div>
      </>
    );
  }
}
export default App;
