import React from "react";
import loadGoogleMapsApi from "load-google-maps-api";
import { GetPontos } from '../../Api'
import { Link } from "react-router-dom";
import { useHistory } from "react-router"
import "./menu.css"
import "./mapa.css";

var markes = []
class Marker {


  constructor(google, lat, long, zoom) {
    this.google = google;
    const mapDom = document.getElementById("map");
    const center = new this.google.LatLng(parseFloat(lat), parseFloat(long));
    const mapTypeId = this.google.MapTypeId.HYBRID;
    this.map = new this.google.Map(mapDom, {
      center,
      zoom,
      mapTypeId,
    });

    mapDom.onload = function () {
      // console.log('ok')
    }
  }

  getMap() {
    return this.map;
  }

  latLong(lat, long) {
    return new this.google.LatLng(parseFloat(lat), parseFloat(long));
  }

  centerMap(zoom, dist) {
    let latMax;
    let latMin;
    let longMax;
    let longMin;
    let retorno_dados = false;

    if (this.map.getZoom() >= zoom) {
      latMax = Math.round(this.map.getCenter().lat() - dist);
      latMin = Math.ceil(this.map.getCenter().lat() + dist);
      longMax = Math.round(this.map.getCenter().lng() - dist);
      longMin = Math.ceil(this.map.getCenter().lng() + dist);

      retorno_dados =
        this._latMax !== latMax ||
        this._latMin !== latMin ||
        this._longMax !== longMax ||
        this._longMin !== longMin;

      if (retorno_dados) {
        this._latMax = latMax;
        this._latMin = latMin;
        this._longMax = longMax;
        this._longMin = longMin;
      }
    }

    if (retorno_dados)
      return {
        latMin: this._latMin,
        latMax: this._latMax,
        longMin: this._longMin,
        longMax: this._longMax,
      };
    else return null;
  }

  getPontos(centro, history) {
    const { latMin, latMax, longMin, longMax } = centro;
    return GetPontos({ latMin, latMax, longMin, longMax }, history)
  }

  criarIcones(dados, trocaTela) {

    try {
      dados.forEach((element) => {
        const { latitude, longitude, link } = element;
        const position = new this.google.LatLng(
          parseFloat(latitude),
          parseFloat(longitude)
        );


        const map = this.map;
        let icon


        if (link.indexOf('http') === -1) {
          icon = `${process.env['REACT_APP_BASE_URL2']}${link}`
        } else {
          icon = link;
        }


        const b = new this.google.Marker({
          map,
          position,
          icon,
        });

        markes.push({
          b,
          c: b.position.lat(),
          d: b.position.lng()
        });


        const div = document.createElement("div");
        const text = document.createElement("text");
        text.innerHTML = element.nome_ponto

        div.appendChild(text);

        if (element.tipo_icone !== "KM") text.style.cursor = 'pointer';


        text.addEventListener('click', function () {
          if (element.tipo_icone === "NÃO") return;
          if (element.pagina !== 'false') trocaTela(element.pagina)
          else return
        })

        const infoWindow = new this.google.InfoWindow();
        b.addListener("click", function () {
          // markes.forEach(x=>{x.b.setMap(null)})
          infoWindow.setContent(div);
          infoWindow.open(this.mapa, b);
        });
      });
    } catch (error) {
      console.log("formato array incorreto");
    }
  }
}


class getUrlVal {
  lista = null;
  constructor(parametros_url) {
    let lista_parametros_url_resolvidas = {};
    const get = (name) => {
      if (
        (name = new RegExp("[?&]" + encodeURIComponent(name) + "=([^&]*)").exec(
          window.location.search
        ))
      )
        return decodeURIComponent(name[1]);
    };

    parametros_url.forEach((element) => {
      lista_parametros_url_resolvidas[element] = get(element);
    });
    this.lista = lista_parametros_url_resolvidas;
  }

  get_list() {
    return this.lista;
  }
}

class carregaMapsClass {
  carregaMapa(funcao, history) {
    const alreadyUsed = (lat, long, zooml) => {

      let latitude;
      let longitude;
      let zoom;

      if ((lat, long, zooml)) {
        latitude = lat;
        longitude = long;
        zoom = Number(zooml);
        window.location.search = "";
      } else {
        latitude = localStorage.getItem("lat");
        longitude = localStorage.getItem("long");
        zoom = Number(localStorage.getItem("zoom"));
      }


      return {
        latitude,
        longitude,
        zoom
      }
    }
    return function (googleMaps) {
      let markerInstancia, map;
      const latLongZoom = new getUrlVal(["lat", "long", "zooml"]);
      let { lat, long, zooml } = latLongZoom.get_list();

      const { latitude, longitude, zoom } = alreadyUsed(lat, long, zooml)
      markerInstancia = new Marker(googleMaps, latitude, longitude, zoom);
      map = markerInstancia.getMap();
      MontaDados();

      map.addListener('zoom_changed', MontaDados)

      map.addListener("drag", MontaDados);
      async function MontaDados() {
        localStorage.setItem("lat", map.getCenter().lat());
        localStorage.setItem("long", map.getCenter().lng());
        localStorage.setItem("zoom", map.getZoom());
        if (document.getElementById("lat"))
          document.getElementById("lat").value = map.getCenter().lat().toFixed(6);
        if (document.getElementById("long"))
          document.getElementById("long").value = map.getCenter().lng().toFixed(6);
        if (document.getElementById("zoom"))
          document.getElementById("zoom").value = map.getZoom();
        const link = `${window.location}?lat=${map.getCenter().lat()}&long=${map.getCenter().lng()}&zooml=${map.getZoom()}`;
        if (document.getElementById("link")) document.getElementById("link").value = link

        const centro = markerInstancia.centerMap(8, 2);
        if (!centro) return;

        const retornoApiPontos = await markerInstancia.getPontos(centro, history);

        if (!retornoApiPontos) return;

        markerInstancia.criarIcones(retornoApiPontos.pontos, funcao);
      }
      if (document.getElementById("control")) document.getElementById("control")
        .addEventListener("click", botaoApertado);
      function botaoApertado() {
        var latLng = markerInstancia.latLong(
          document.getElementById("lat").value,
          document.getElementById("long").value
        );
        map.panTo(latLng);
        map.setZoom(Number(document.getElementById("zoom").value));
        MontaDados();
      }
    }
  }
}



function firstUse() {
  if (
    !localStorage.getItem("lat") ||
    !localStorage.getItem("long") ||
    !localStorage.getItem("zoom")
  ) {
    localStorage.setItem("lat", "-21");
    localStorage.setItem("long", "-50");
    localStorage.setItem("zoom", "9");
  }
}

async function init(trocaTela, history) {
  firstUse();
  const maps = new carregaMapsClass()
  loadGoogleMapsApi({ key: "AIzaSyBZkRztzytJboga-ppS__Lb8YbettZs8Rk" })
    .then(maps.carregaMapa(trocaTela, history));
}

function sizeOfThings() {
  var windowHeight = window.innerHeight;
  return windowHeight
};

function resize() {
  window.addEventListener('resize', function () {
    const altura = sizeOfThings();
    const mapa = document.getElementById('map')
    if (mapa)
      mapa.style.height = altura + 'px'
  });
}

