import React, { useState } from "react";
import Pagina from "./pages/pagina/Pagina.js";
import Mapa from "./pages/mapa/Mapa.js";
import Login from "./pages/login/Login.js";
import Admin from "./pages/admin/Admin.js";
import CadastroPonto from './pages/admin/CadastroPonto';
import CadastraCliente from './pages/admin/CadastraCliente';
import { BrowserRouter, Route } from "react-router-dom";

document.getElementById("loader").style.display = "none";

export default function () {
  const [id_pagina, setId_pagina] = useState();
  const [cria_ponto, setcria_ponto] = useState();
  const [perfil_usuario, setperfil_usuario] = useState();

  function trocaTela(element) {
    setId_pagina(element);
  }

  function mapa() {
    if (!id_pagina)
      return <Mapa
        trocaTela={trocaTela}
        perfil_usuario={perfil_usuario}
        cria_ponto={cria_ponto} />;
  }

  function pagina() {
    if (id_pagina)
      return <Pagina id_pagina={id_pagina} trocaTela={trocaTela} />;
  }

  function CoreApp(props) {
    return (<>
      {mapa(props)}
      {pagina()}
    </>)
  }


  function tt(k) {
    setcria_ponto(k.criaPonto)
    setperfil_usuario(k.perfilUsuario)
  }

  return (
    <BrowserRouter
      basename="/app"
      className='container'
    >
      <Route exact render={(props => <CoreApp {...props} cria_ponto={cria_ponto} />)} path="/" />
      <Route exact render={(props => <Login {...props} kk={tt} />)} path="/Login" />

      <Route exact render={(props => <Admin {...props}
        perfil_usuario={perfil_usuario}
        cria_ponto={cria_ponto} />)} path="/Admin" />


      <Route exact render={(props => <CadastroPonto {...props} />)} path="/CadastroPonto" />
      <Route exact render={(props => <CadastraCliente {...props} />)} path="/CadastraCliente" />
    </BrowserRouter>
  );
};
