import React, { useRef } from 'react';
import { ApiAutenticacao } from '../../Api'
import { useHistory } from "react-router"

async function envia(history, props) {
    const PayLoadLogin = JSON.stringify({
        'login': document.getElementById('login').value,
        'senha': document.getElementById('senha').value
    })
    const retornoAutenticacao = await ApiAutenticacao(history, PayLoadLogin)
    if (retornoAutenticacao !== undefined)
        props.kk(retornoAutenticacao)
}

function sizeOfThings() {
    var windowHeight = window.innerHeight;
    return windowHeight
};

export default (props) => {
    let history = useHistory()
    const inputEl = useRef(null);
    return (
        <div
            className='row'
            style={{
                backgroundColor: 'white',
                height: sizeOfThings() - 17
            }}
        >

            <form onSubmit={(event) => {
                event.preventDefault();
                envia(history, props)
            }}>



                <div className='col-md-4 offset-md-4'
                    style={{
                        marginTop: '7%'
                    }}
                >
                    <div
                        className="alert alert-danger col-md-11"

                        role="alert"
                        id="login_fail"
                        style={{
                            display: "none"
                        }}>
                        Usuário e senha incorretos..
                    </div>
                    <div className='row'>




                        <div className='row'>



                            <div className='col-md-6'>
                                <img
                                    ref={inputEl}
                                    src={`${process.env['REACT_APP_BASE_URL3']}imagem/logo.jpeg`}
                                    style={{
                                        padding: 20,
                                        borderRadius: 25,
                                        width: '200%',
                                        height: '100%'
                                    }}
                                    alt='LOGO BAHM'
                                />
                            </div>


                        </div>




                    </div>
                    <div className='row'>

                        <div className='col-md-11'>
                            <input type="text"
                                id="login"
                                name="email"
                                className='form-control'
                                placeholder="Usuário" />

                            <input
                                id="senha"
                                type="password"
                                name="pass"
                                placeholder="Senha"
                                className='form-control'
                                autoComplete='on'
                                style={{
                                    marginTop: '2%'
                                }} />

                            <button style={{
                                backgroundColor: 'blue',
                                color: 'white',
                                padding: 20,
                                borderRadius: 25,
                                marginTop: '10%',
                                width: '100%'
                            }}>
                                ENTRAR
                            </button>

                        </div>
                    </div>
                </div>





            </form>
        </div>
    );
};